.profile__id-title {
	font-family: Inter, Arial, sans-serif;
	font-style: normal;
	font-weight: 500;
	font-size: 42px;
	line-height: 48px;
	margin: 0;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

@media (min-width: 320px) and (max-width: 768px) {
	.profile__id-title {
		max-width: 189px;
		font-size: 27px;
		line-height: 33px;
		text-align: center;
		margin: 0 0 0 27px;
	}
}