.authorization__button {
    width: 358px;
    height: 50px;
    color: #fff;
    background-color: #fff;
    margin: 216px auto 0;
    text-align: center;
    border-radius: 2px;
    border: none;
    padding: 0;
    color: #000;
    cursor: pointer;
    transition: opacity 0.5s ease;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
}

.authorization__button:hover {
    opacity: 0.6;
} 