.footer {
	color: #545454;
	font-family: Inter,Arial, sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 22px;
	margin: 66px auto 60px;
	max-width: 880px;
}

@media (min-width: 469px) and (max-width: 1023px) {
	.footer {
		max-width: calc(100% - 100px);
	}
}

@media (min-width: 320px) and (max-width: 468px) {
	.footer {
		margin: 48px 19px 36px;
		max-width: 100%;
	}
}